import React, { useMemo, useState } from 'react'
import reactDom from 'react-dom';
import { useLocation } from 'react-router-dom';
import { generarClassSchema } from '../../helpers/generarClassSchema';
import { queryStringHelper } from '../../helpers/queryStringHelper';
import { useForm } from '../../hooks/useForm';
import { getJugadores } from '../../selectors/getJugadores';
import { PopHandleJugador } from '../playground/PopHandleJugador';

export const MyElevenScreen = ({ history }) => {

    // ARRAY DE JUGADORES ALINEADOS
    const [jugadoresAlineados, setJugadoresAlineados] = useState([]);

    // Esquemas
    const esquemas = ['3-4-3', '3-5-2', '4-3-3', '4-4-2', '4-5-1', '5-3-2', '5-4-1'];

    // Esquema seleccionado
    const [esquemaSeleccionado, setEsquemaSeleccionado] = useState('4-3-3');

    // Procesamos el PARAM de la URL
    const location  = useLocation();
    let   queryText = '';
    if( location.search.indexOf('q=') !== -1 )
    {
        const { q } = queryStringHelper(location.search);
        queryText = decodeURI(q);
    }

    // Cargamos el HOOK de FORMULARIO
    const { values:formValues, handleInputChange } = useForm({ searchText : queryText });
    const { searchText } = formValues;

    // Cargamos la lista de jugadores aplicando el filtro
    const jugadoresFiltrados = useMemo(() => getJugadores(queryText), [ queryText ]);

    // Función de submit del FORM
    const handleSearch = (e) => {
        e.preventDefault();
        history.push(`?q=${ searchText }`);
    }

    // Cambio de esquema
    const handleChangeSchema = (e) => {
        
        let sch = e.target.attributes.dataref.value;
        setEsquemaSeleccionado(sch);

        // Comprobamos si debemos quitar jugadores del 11 por el cambio de esquema
        let aux_sch       = sch.split('-');
        let clases_cribar = [];
        aux_sch.forEach((linea, indice) => {
            switch (indice)
            {
                case 0:
                    if( linea === '3' )
                        clases_cribar.push('df_4', 'df_5');
                    else if( linea === '4' )
                        clases_cribar.push('df_5');
                    break;
                case 1:
                    if( linea === '3' )
                        clases_cribar.push('md_4', 'md_5');
                    else if( linea === '4' )
                        clases_cribar.push('md_5');
                    break;
                case 2:
                    if( linea === '1' )
                        clases_cribar.push('dl_2', 'dl_3');
                    else if( linea === '2' )
                        clases_cribar.push('dl_3');
                    break;
                default:
                    break;
            }
        });

        let aux_clase     = '';
        let aux_jugadores = jugadoresAlineados.filter((jugador) => {
            aux_clase = jugador.clase.split(' ');
            return clases_cribar.indexOf(aux_clase[2]) === -1;
        });

        setJugadoresAlineados(aux_jugadores);

        let str_class = generarClassSchema(sch);
        document.querySelector('#playground__players').classList = str_class;
    }

    // Función click jugador de la búsqueda
    const handleClickJugador = (club, nombre, posicion) => {

        let props_pop = {
            club                 : club,
            nombre               : nombre,
            posicion             : posicion,
            esquemaSeleccionado  : esquemaSeleccionado,
            jugadoresAlineados   : jugadoresAlineados,
            setJugadoresAlineados: setJugadoresAlineados
        }

        document.querySelector('#luter').classList.add('on');

        reactDom.render(
            <PopHandleJugador props = { props_pop } />,
            document.getElementById('luter')
        )
    }

    // ELIMINAR JUGADOR
    const handleDeletePlayer = (indice) => {
        let aux_jugadores = jugadoresAlineados;
        aux_jugadores.splice(indice, 1);

        setJugadoresAlineados(
            [ ...aux_jugadores ]
        );
    }

    return (
        <div className="myeleven__container contenedor">
            <div className="panel_left">
                <div className="schemas__box">
                    <div className="title">Esquema</div>
                    <ul className="schemas__list">
                        {
                            esquemas.map((esquema) => {
                                return (
                                    <li
                                        onClick   = { handleChangeSchema }
                                        key       = { esquema }
                                        dataref   = { esquema }
                                        className = { (esquemaSeleccionado === esquema ? 'selected':'') }
                                    >
                                        { esquema }
                                    </li>
                                )
                            })
                        }
                    </ul>
                </div>
                <div className="myelevens__box">
                    <div className="title">Mis onces</div>
                    <div className="myelevens__list">
                        
                    </div>
                </div>
            </div>
            <div className="playground">
                <div id="playground__players" className="defensa_4 medio_3 delantera_3">
                    {
                        ( jugadoresAlineados.length > 0 ?   
                            jugadoresAlineados.map((jugador, i) => {
                                return (
                                    <div
                                        key       = { i }
                                        className = { jugador.clase }
                                        onClick   = { () => {
                                            handleDeletePlayer(i);
                                        } }
                                    >
                                        <img src={ `./assets/img/clubes/${ jugador.club }.png` } alt={ jugador.nombre } />
                                        <p>{ jugador.nombre }</p>
                                    </div>
                                )
                            })
                            :
                            ''
                        )
                    }
                </div>
            </div>
            <div className="panel_right">
                <form onSubmit={ handleSearch }>
                    <div className="input_box">
                        <input
                            className   = "input_std"
                            type        = "text"
                            name        = "searchText"
                            placeholder = "Buscar jugadores..."
                            value       = { searchText }
                            onChange    = { handleInputChange }
                        />
                        <button type="submit" className="btn_search">Buscar</button>
                        <span className="limpia"></span>
                    </div>
                </form>
                <div className="myeleven__results">
                    {
                        (jugadoresFiltrados.length === 0 ? // IF
                            <div className="search_default">Sin resultados</div>
                        : // ELSE
                            jugadoresFiltrados.map( ({ club, dorsal, nombre, posicion }) => {

                                let min_position = posicion.slice(1,3);

                                return (
                                    <div
                                        key       = { dorsal }
                                        className = { `player ${ min_position }` }
                                        onClick   = { () => {
                                            handleClickJugador(club, nombre, min_position);
                                        } }
                                    >
                                        <span className="club" style={{ backgroundImage: `url(./assets/img/clubes/${ club }.png)` }}></span>
                                        <span className="position"></span>
                                        <span className="name">{ nombre }</span>
                                    </div>
                                )
                            })
                        )
                    }
                </div>
                <div className="myeleven__buttons">
                    <button>Guardar once</button>
                </div>
            </div>
            <span className="limpia"></span>
        </div>
    )
}
