import React from 'react';
import {
    BrowserRouter as Router,
    Route,
    Switch,
  } from "react-router-dom";
import { Header } from '../components/UI/Header';
import { ClubScreen } from '../components/Screens/ClubScreen';
import { InicioScreen } from '../components/Screens/InicioScreen';
import { ClubesScreen } from '../components/Screens/ClubesScreen';
import { MyElevenScreen } from '../components/Screens/MyElevenScreen';
import { Footer } from '../components/UI/Footer';
import { Luter } from '../components/UI/Luter';

export const AppRouter = () => {

    return (
        <Router>
            <Header />

            <Switch>
                <Route exact path="/" component={ InicioScreen } />
                <Route exact path="/clubes" component={ ClubesScreen } />
                <Route exact path="/clubes/:clubId" component={ ClubScreen } />
                <Route exact path="/my-eleven" component={ MyElevenScreen } />
            </Switch>

            <Footer />
            <Luter />
        </Router>
    )
}
