export const generarClassSchema = (str_schema) => {

    let str_class = '';

    if( str_schema !== '' )
    {
        let aux_sch = str_schema.split('-');
    
        // Línea defensiva
        switch (aux_sch[0]) {
            case '3':
                str_class += ' defensa_3';
                break;
            case '4':
                str_class += ' defensa_4';
                break;
            case '5':
                str_class += ' defensa_5';
                break;
            default:
                str_class += '';
        }

        // Linea medio
        switch (aux_sch[1]) {
            case '3':
                str_class += ' medio_3';
                break;
            case '4':
                str_class += ' medio_4';
                break;
            case '5':
                str_class += ' medio_5';
                break;
            default:
                str_class += '';
        }

        // Linea delantera
        switch (aux_sch[2]) {
            case '1':
                str_class += ' delantera_1';
                break;
            case '2':
                str_class += ' delantera_2';
                break;
            case '3':
                str_class += ' delantera_3';
                break;
            default:
                str_class += '';
        }
    }

    return str_class;
}