import React from 'react';
import { NavLink } from 'react-router-dom';

export const Header = () => {
    return (
        <header>
            <span className="logo"></span>
            <nav>
                <ul>
                    <li>
                        <NavLink exact activeClassName="selected" to="/">Inicio</NavLink>
                    </li>
                    <li>
                        <NavLink exact activeClassName="selected" to="/my-eleven">My Eleven</NavLink>
                    </li>
                    <li>
                        <NavLink activeClassName="selected" to="/clubes">Clubes</NavLink>
                    </li>
                </ul>
            </nav>
        </header>
    )
}
