import { plantillas } from "../data/plantillas"

export const getJugadores = (searchText) => {

    // Preprocesamos el searchText
    searchText = searchText.toLowerCase().trim();
    let jugadores = [];

    if( searchText.length > 2 )
    {
        let clubes          = Object.keys(plantillas);
        let aux_jugadores   = Object.values(plantillas);
        let reaux_jugadores = [];
        let aux_nombre      = '';
    
        // Comprobamos si lo que se ha buscado es un club
        let aux_searchText = searchText.replace(' ', '-');
        if( clubes.includes(aux_searchText) )
        {
            // Para poder pintar el escudo en cada jugador
            for(let i = 0; i < clubes.length; i++)
            {
                if( clubes[i].includes(aux_searchText) )
                {
                    reaux_jugadores = aux_jugadores[i];
    
                    for(let z = 0; z < reaux_jugadores.length; z++)
                    {
                        // Guardo el equipo para pintar el escudo
                        reaux_jugadores[z] = {
                            ...reaux_jugadores[z],
                            club: clubes[i]
                        }
                        jugadores.push(reaux_jugadores[z]);
                    }
                }
            }
        }
        else
        {
            for(let i = 0; i < aux_jugadores.length; i++)
            {
                reaux_jugadores = aux_jugadores[i];
    
                for(let z = 0; z < reaux_jugadores.length; z++)
                {
                    aux_nombre = reaux_jugadores[z].nombre.toLocaleLowerCase();
                    if( aux_nombre.includes(searchText) )
                    {
                        // Guardo el equipo para pintar el escudo
                        reaux_jugadores[z] = {
                            ...reaux_jugadores[z],
                            club: clubes[i]
                        }
                        jugadores.push(reaux_jugadores[z]);
                    }
                }
            }
        }
    }

    return jugadores;
}
