import React from 'react'

export const InicioScreen = () => {

    return (
        <div className="mainbox contenedor animate__animated animate__fadeIn">
            <h2 className="titulo animate__animated animate__fadeInDown animate__delay-1s">Bienvenido a My Eleven</h2>
            <p className="descripcion animate__animated animate__fadeInUp animate__delay-1s">
                La plataforma donde gestionarás <strong>tus 11s</strong> de la liga, a tu gusto. Prueba distintas alineaciones, ¡combina como quieras!.
            </p>
        </div>
    )
}
