import React, { useMemo } from 'react'
import { useParams } from 'react-router-dom';
import { getInfoClub } from '../../selectors/getInfoClub';
import { getPlantilla } from '../../selectors/getPlantilla';

export const ClubScreen = ({ history }) => {

    const { clubId } = useParams();

    const plantilla = useMemo(() => getPlantilla(clubId), [ clubId ]);
    const infoClub  = useMemo(() => getInfoClub(clubId), [ clubId ]);

    if( plantilla.length === 0 || !infoClub.hasOwnProperty('uri') )
        history.goBack();

    const handleBack = () => {
        history.goBack();
    }

    return (
        <div className="plantilla__container contenedor w_1600">
            <div className={ `plantilla__luter ${ infoClub.textClass }` } style={ infoClub.gradient }>
                <div className="club_name animate__animated animate__fadeIn animate__delay-250ms">{ infoClub.nombre }</div>
                <img className="club_logo animate__animated animate__fadeInDown animate__delay-250ms" src={ `/assets/img/clubes/${ infoClub.foto }` } alt={ infoClub.nombre } />
                <div className="matriz__cards animate__animated animate__fadeInUp animate__delay-500ms">
                    {
                        plantilla.map(({ dorsal, nombre, posicion }) => {
                
                            let min_position = posicion.slice(1,3);

                            return (
                                <div
                                    key       = { dorsal }
                                    className = { `card ${ min_position }` }
                                >
                                    <span className="position"></span>
                                    <span className="name">{ nombre }</span>
                                    <span className="dorsal">{ dorsal }</span>
                                </div>
                            )
                        })
                    }
                </div>
                <button
                    className = "btn_back"
                    onClick   = { handleBack }
                >
                    Volver
                </button>
            </div>
        </div>
    )
}
