import React from 'react'
import reactDom from 'react-dom';

export const PopHandleJugador = ({props}) => {

    const { club, nombre, posicion, esquemaSeleccionado, jugadoresAlineados, setJugadoresAlineados } = props;

    // Controlamos el esquema seleccionado y la posición del jugador
    let class_esquema = '';
    let linea_esquema = esquemaSeleccionado.split('-');
    switch (posicion)
    {
        case 'PT':
            class_esquema += ' sw_1';
            break;
        case 'DF':
            class_esquema += ` sw_${ linea_esquema[0] }`;
            break;
        case 'MC':
            class_esquema += ` sw_${ linea_esquema[1] }`;
            break;
        case 'DL':
            class_esquema += ` sw_${ linea_esquema[2] }`;
            break;
        default:
            break;
    }

    const handleCancelar = () => {
        document.querySelector('#luter').classList.remove('on');
        reactDom.unmountComponentAtNode(document.getElementById('luter'));
    }

    // Función para alinear jugador
    const handleAlinearJugador = (posicion_linea) => {  
        
        let classPosicion = 'playground__player';
        switch( posicion ) {
            case 'PT':
                classPosicion += ' portero';
                break;
            case 'DF':
                classPosicion += ` defensa df_${ posicion_linea }`;
                break;
            case 'MC':
                classPosicion += ` medio md_${ posicion_linea }`;
                break;
            case 'DL':
                classPosicion += ` delantero dl_${ posicion_linea }`;
                break;
            default:
                classPosicion += '';
        }

        // Buscamos si ya hay un jugador en esa misma posición para quitarlo
        // FILTRAMOS POSICIÓN
        let aux_jugadores = jugadoresAlineados.filter((jugador) => { 
            return jugador.clase !== classPosicion;
        });

        // FILTRAMOS NOMBRES
        aux_jugadores = aux_jugadores.filter((jugador) => { 
            return jugador.nombre !== nombre;
        });

        setJugadoresAlineados(
            [
                ...aux_jugadores,
                {
                    club    : club,
                    nombre  : nombre,
                    posicion: posicion,
                    clase   : classPosicion
                }
            ]
        );

        document.querySelector('#luter').classList.remove('on');
        reactDom.unmountComponentAtNode(document.getElementById('luter'));
    }
    
    // Para pintar las posiciones
    const selectablePositions = [1,2,3,4,5];

    return (
        <div className="pop_gest_jugador animate__animated animate__fadeInUp">
            <span className="close_pop" onClick={ handleCancelar }><i className="fas fa-times"></i></span>
            <div className="title">Posición del jugador</div>
            <p className="description">Las posiciones se sitúan de izquierda a derecha del campo.</p>
            <div className={ `switch_positions ${ class_esquema }` }>
                {
                    selectablePositions.map((posicion_seleccionable) => {
                        return (
                            <span
                                key       = { `pop_p${ posicion_seleccionable }` }
                                className = { `sw_position ps_${ posicion_seleccionable }` }
                                onClick   = { () => {
                                    handleAlinearJugador(posicion_seleccionable)
                                } }
                            >
                                { posicion_seleccionable }
                            </span>
                        )
                    })
                }
                <span className="limpia"></span>
            </div>
            <button
                onClick   = { handleCancelar }
                className = "btn_cancel"
            >
                Cancelar
            </button>
        </div>
    )
}
