import React from 'react';
import { Link } from 'react-router-dom';
import { clubes } from '../../data/clubes';

export const ClubesScreen = React.memo(() => {
    
    return (
        <div className="matriz contenedor w_1600">
            {
                clubes.map((club) => {
                    return (
                        <div
                            key       = { club.nombre }
                            className = "item animate__animated animate__fadeIn"
                        >
                            <Link to={ `./clubes/${ club.uri }` }>
                                <img src={ `/assets/img/clubes/${ club.foto }` } />
                            </Link>
                            <span className="title">{ club.nombre }</span>
                        </div>
                    )
                })
            }
        </div>
    )
})
